import React from "react";
import { Link, useParams } from "react-router-dom";

import Navbar from "../../components/navbar";
import Blogs from '../../components/blogs'
import BlogSidebar from "../../components/blog-sidebar";
import Footer from "../../components/footer";
import Switcher from "../../components/switcher";

import blogImg from "../../assets/images/bg/7.jpg"
import { blogData } from "../../data/data";

export default function KrugarNationalPark(){

    
    let params = useParams();
    let id = params.id
    let data = blogData.find((item)=>item.id === parseInt(id))
    return(
        <>
        <Navbar navclass="defaultscroll is-sticky" navlight={true} manuclass="justify-end nav-light"/>
        <section className="relative table w-full items-center py-36 bg-[url('../../assets/images/bg/7.jpg')] bg-top bg-no-repeat bg-cover">
            <div className="absolute inset-0 bg-gradient-to-b from-slate-900/60 via-slate-900/80 to-slate-900"></div>
            <div className="container relative">
                <div className="grid grid-cols-1 pb-8 text-center mt-10">
                    <h3 className="text-4xl leading-normal tracking-wider font-semibold text-white">{data?.title ? data.title : 'How to make the most of your bush break adventure at Kruger National Park'}</h3>

                    <ul className="list-none mt-6">
                        <li className="inline-block text-white/50 mx-5"> <span className="text-white block">Author :</span> <span className="block">Cape Archives</span></li>
                        <li className="inline-block text-white/50 mx-5"> <span className="text-white block">Date :</span> <span className="block">{data?.date ? data.date : '19th June 2024'}</span></li>
                        <li className="inline-block text-white/50 mx-5"> <span className="text-white block">Time :</span> <span className="block">8 Min Read</span></li>
                    </ul>
                </div>
            </div>
            
            <div className="absolute text-center z-10 bottom-5 start-0 end-0 mx-3">
                <ul className="tracking-[0.5px] mb-0 inline-block">
                    <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out text-white/50 hover:text-white"><Link to="/">Cape Archives Tours</Link></li>
                    <li className="inline-block text-base text-white/50 mx-0.5 ltr:rotate-0 rtl:rotate-180"><i className="mdi mdi-chevron-right"></i></li>
                    <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out text-white" aria-current="page">Blog Detail</li>
                </ul>
            </div>
        </section>
        <section className="relative md:py-24 py-16">
    <div className="container">
        <div className="grid md:grid-cols-12 grid-cols-1 gap-6">
            <div className="lg:col-span-8 md:col-span-6">
                <div className="relative overflow-hidden rounded-md shadow dark:shadow-gray-800">
                    <img src={data?.image ? data.image : blogImg} alt=""/>
                    <div className="p-6">
                        <p className="text-slate-400">Did you know Kruger National Park (KNP) is home to not only the Big 5, but several other species? If were talking numbers, there are 336 types of trees, 49 fish, 34 amphibians, 114 reptiles, 507 birds and 147 mammals. In other words, 2 million hectares of land is a refuge and food source for diverse wildlife. Why the global attraction to this large stretch of land in the Mpumalanga Lowveld? Firstly, Kruger is considered a premier wildlife destination, especially if youre desperate to spot the Big 5. Stats from 2020 showed the park had an estimated 13,000 elephants, 40,000 buffalo and 2,000 lions. Considered South Africas largest wildlife sanctuary, the public-entry game park has been lauded for its environmental management systems and policies, thanks to the unrivaled dedication of South African National Parks (SANParks) and hundreds of community volunteers. If youre a first-time visitor to the game reserve, heres a list of things you need to know to have the safari experience of a lifetime.</p>
                        <div className="mt-3">
                            <p className="text-slate-400"><strong>Essential Packing List</strong></p>
                            <p className="text-slate-400">Its advisable to visit during the rainy season between October and April when the land turns a lush green and migratory birds are on full display. And when it rains, its baby-making season. Hopefully, youll be lucky enough to witness the birth of a calf. Despite the cold, wet weather, mosquitoes are a nuisance, no matter the time of year. Bring with bug spray, a rain jacket, a warm coat, lots of water and snacks while on a game drive. Dont forget your binoculars and a decent camera to capture those epic moments.</p>
                        </div>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Self-drive or Guided Tour?</strong></p>
                            <p className="text-slate-400">Both these options have their positives. For instance, a self-drive gives you the freedom and flexibility to explore the park at your own pace and in your own time. A self-drive is also the cheapest of the two choices. There are entrance gates on the southern, central and northern sides of the park. The roads are regularly maintained and in good condition. However, you should download a map onto your device or phone on the off chance you get lost. Also, before entering, take note of the opening and closing times. A guided tour on a safari vehicle may cost a bit more, but it affords you the luxury of sitting back, relaxing and enjoying the game drive. Professional guides know the roads well and can take you to the best viewing sights. As far as safety is concerned, theyre equipped with riffles if some animals get too close for comfort.</p>
                        </div>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Best Viewing Spots</strong></p>
                            <p className="text-slate-400">Youll often hear tourists complain about not seeing much when visiting a game reserve. The most exciting part was watching a few springboks skipping to the watering hole or vultures feasting on the last of a forgotten kill. What they want is a daring encounter with the mighty lion or to catch a glimpse of the elusive rhino. Disappointment is part and parcel of a game drive. Sometimes you have to lower your expectations and think of it as a game of Hide-and-Seek. As previously mentioned, winter provides the perfect cover for wildlife that gathers near the water. Places like watering holes, dams and rivers are the most likely spots to view game, either early morning or evening.</p>
                        </div>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Conservation Efforts</strong></p>
                            <p className="text-slate-400">With the animal kingdom constantly battling their dwindling habitat, so does Kruger National Park have to deal with poaching and threats of extinction. Climate change and the international demand for ivory puts many endangered species at risk, hence the need for SANParks to partner with local conversation agencies. To get an idea of the parks conservation efforts, tourists are welcome to visit some of the initiatives and can offer a cash donation. In addition, every visitor pays a conservation fee for every day spent inside Kruger National Park. These funds are used towards the upkeep of the reserve and conserve its natural and cultural heritage.</p>
                        </div>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Historical Sites</strong></p>
                            <p className="text-slate-400">Yes, Kruger Park has big game sightings, but its cultural heritage is just as important. There are more than 250 recorded archaeological sites dating as far back as the Stone Age - about 1 million years ago. Sites that are currently open to the public are: Albasini Ruins, Masorini Ruins, Thulamela. Whatever you do, dont break the rules. Safety is of paramount importance; not only for visitors but wildlife as well. Passengers should stay in their vehicles at all times, including vehicle doors remaining closed and no protruding parts of the body. Stay within the speed limit and off roads with no entry signs. Just as you wouldnt feed animals at a zoo, the same applies here. Wild animals cant differentiate between litter and food, so leave nothing behind. Lastly, stick to the gate closing times.</p>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Accommodation Choices</strong></p>
                            <p className="text-slate-400">Kruger National Park doesnt fall short on accommodation types to suit all styles and budgets. Visitors can choose from private game lodges which are within park boundaries. These offer inclusive game drives and luxury-tented camps. Animals often roam freely in this borderless state. Dont be surprised if a giraffe waltzes past your verandah while enjoying your first cup of morning coffee. Standard rest camps are on a self-catering basis and are preferred by those who want a rustic and budget-friendly holiday. Rest camp accommodation ranges from guesthouses to cottages and camping sites.</p>
                        </div>
                        <div className="mt-6">
                            <p className="text-slate-400"><strong>Planning Your Trip</strong></p>
                            <p className="text-slate-400">Planning a trip to Kruger National Park? Contact Cape Archives Tours for expert guidance and assistance. Our team will help you plan your dream safari experience, from booking accommodations to arranging guided tours and ensuring you have all the essentials for a memorable adventure. Let us take the stress out of planning so you can focus on enjoying the wonders of one of Africas most iconic wildlife destinations.</p>
                        </div>
                    </div>
                </div>
            <div className="p-6 rounded-md shadow dark:shadow-gray-800 mt-8">
            <h5 className="text-lg font-semibold">Leave A Comment:</h5>
            <form className="mt-8">
                    <div className="grid lg:grid-cols-12 lg:gap-6">
                        <div className="lg:col-span-6 mb-5">
                            <div className="text-left">
                                <label htmlFor="name" className="font-semibold">Your Name:</label>
                                <input name="name" id="name" type="text" className="mt-3 w-full py-2 px-3 h-10 bg-transparent dark:bg-slate-900 dark:text-slate-200 rounded outline-none border border-gray-100 dark:border-gray-800 focus:ring-0" placeholder="Name :"/>
                            </div>
                        </div>

                        <div className="lg:col-span-6 mb-5">
                            <div className="text-left">
                                <label htmlFor="email" className="font-semibold">Your Email:</label>
                                <input name="email" id="email" type="email" className="mt-3 w-full py-2 px-3 h-10 bg-transparent dark:bg-slate-900 dark:text-slate-200 rounded outline-none border border-gray-100 dark:border-gray-800 focus:ring-0" placeholder="Email :"/>
                            </div>
                        </div>
                    </div>

                    <div className="grid grid-cols-1">
                        <div className="mb-5">
                            <div className="text-left">
                                <label htmlFor="comments" className="font-semibold">Your Comment:</label>
                                <textarea name="comments" id="comments" className="mt-3 w-full py-2 px-3 bg-transparent dark:bg-slate-900 dark:text-slate-200 rounded outline-none border border-gray-100 dark:border-gray-800 focus:ring-0 h-28" placeholder="Message :"></textarea>
                            </div>
                        </div>
                    </div>
                    <button type="submit" id="submit" name="send" className="py-2 px-5 inline-block tracking-wide align-middle duration-500 text-base text-center bg-red-500 text-white rounded-md w-full">Send Message</button>
                </form>
            </div>
            </div>
            </div>

        <BlogSidebar/>
    </div>
</div>

<Blogs/>
</section>

        <Footer/>
        <Switcher/>
        </>
    )
}