import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import Navbar from "../../../components/navbar";
import Footer from "../../../components/footer";
import Switcher from "../../../components/switcher";
import { packages } from "../../../data/data";
import { Link } from "react-router-dom";

export default function Chauffeur() {
    const { id } = useParams();
    const tour = packages.find((tour) => tour.id === parseInt(id));

    useEffect(() => {
        // Dynamically load Bokun widget script
        const script = document.createElement("script");
        script.src = "https://widgets.bokun.io/assets/javascripts/apps/build/BokunWidgetsLoader.js?bookingChannelUUID=e22fa730-5f47-4a57-b871-b816ed72e113";
        script.async = true;
        document.body.appendChild(script);

        return () => {
            // Cleanup: remove the script when the component unmounts
            document.body.removeChild(script);
        };
    }, []);

    return (
        <>
            <Navbar navclass="defaultscroll is-sticky" navlight={true} manuclass="justify-end nav-light" />
            <section className="relative table w-full items-center py-36 bg-[url('../../assets/images/bg/cta.jpg')] bg-top bg-no-repeat bg-cover">
                <div className="absolute inset-0 bg-gradient-to-b from-slate-900/60 via-slate-900/80 to-slate-900"></div>
                <div className="container relative">
                    <div className="grid grid-cols-1 pb-8 text-center mt-10">
                        <h3 className="text-3xl leading-normal tracking-wider font-semibold text-white">{packages.title}</h3>
                    </div>
                </div>

                <div className="absolute text-center z-10 bottom-5 start-0 end-0 mx-3">
                    <ul className="tracking-[0.5px] mb-0 inline-block">
                        <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out text-white/50 hover:text-white"><Link to="/">Cape Archives Tours</Link></li>
                        <li className="inline-block text-base text-white/50 mx-0.5 ltr:rotate-0 rtl:rotate-180"><i className="mdi mdi-chevron-right"></i></li>
                        <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out text-white" aria-current="page">Tour</li>
                    </ul>
                </div>
            </section><div className="mt-8">
            <div className="bokunWidget" data-src="https://widgets.bokun.io/online-sales/e22fa730-5f47-4a57-b871-b816ed72e113/experience/425092"></div>
            <noscript>Please enable javascript in your browser to book</noscript>
            </div>
            <Footer />
            <Switcher />
        </>
    );
}
