import React from "react";
import { Link } from "react-router-dom";

import Navbar from "../../components/navbar";
import Footer from "../../components/footer";
import Switcher from "../../components/switcher";

import { restrictions } from "../../data/data";

export default function Privacy(){
    return(
        <>
        <Navbar navclass="defaultscroll is-sticky" navlight={false} manuclass="justify-end"/>

        <section className="relative table w-full py-32 lg:py-40 bg-gray-50 dark:bg-slate-800">
            <div className="container relative">
                <div className="grid grid-cols-1 text-center mt-10">
                    <h3 className="text-3xl leading-normal font-semibold">Privacy Policy</h3>
                </div>
            </div>
            
            <div className="absolute text-center z-10 bottom-5 start-0 end-0 mx-3">
                <ul className="tracking-[0.5px] mb-0 inline-block">
                    <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out hover:text-red-500"><Link to="/">Cape Archives Tours</Link></li>
                    <li className="inline-block text-base text-slate-950 dark:text-white mx-0.5 ltr:rotate-0 rtl:rotate-180"><i className="mdi mdi-chevron-right"></i></li>
                    <li className="inline-block uppercase text-[13px] font-bold duration-500 ease-in-out hover:text-red-500"><Link to="">Utility</Link></li>
                    <li className="inline-block text-base text-slate-950 dark:text-white mx-0.5 ltr:rotate-0 rtl:rotate-180"><i className="mdi mdi-chevron-right"></i></li>
                    <li className="inline-block uppercase text-[13px] font-bold text-red-500" aria-current="page">Privacy Policy</li>
                </ul>
            </div>
        </section>

        <section className="relative md:py-24 py-16">
            <div className="container relative">
                <div className="md:flex justify-center">
                    <div className="md:w-full">
                        <div className="p-6 bg-white dark:bg-slate-900 shadow dark:shadow-gray-800 rounded-md">
                            <h5 className="text-xl font-semibold mb-4 mt-8">Personal data may either be collected directly from you, as data subject, or it may be collected from other sources such as:</h5>
                            <ul className="list-none text-slate-400 mt-4">
                                {restrictions.map((item,index)=>{
                                    return(
                                        <li className="flex mt-2" key={index}><i className="mdi mdi-chevron-right text-red-500 text-lg align-middle me-2"></i>{item}</li>
                                    )
                                })}
                            </ul>
                            <p className="text-slate-400 mt-3">Where information is not collected directly from you as a data subject, we will take reasonable steps to inform you of the fact that the information was collected and will be processed.</p>
                            <p className="text-slate-400 mt-3">You must not send us personal information about someone else without first getting his or her consent for it to be used and disclosed in the ways set out in this terms and conditions. This is because we will assume he or she has consented although we may still ask for confirmation from them. Where you do give us information about someone else, or someone else discloses a connection with you, that information may be considered with your other personal information.</p>
                            <p className="text-slate-400 mt-3">For us to provide you with the best service, we need to collect and process personal information from you as a customer. This information will consist of the following</p>
                            <p className="text-slate-400 mt-3">Personal information, Purpose for processing, Legal basis for processing</p>
                            <p className="text-slate-400 mt-3">Full name and Identifying details (including Identity or passport numbers) and Contact details, Client file, Processing billing and Travel records</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <Footer/>
        <Switcher/>
        </>
    )
}
