import React,{ useEffect } from "react";
import AOS from 'aos'
import "aos/dist/aos.css";
import { Wrapper,Content } from "./story.style";
import xav from "../assets/images/about/xavier.jpg"
import vic from "../assets/images/about/vicfalls.JPG"
import part from "../assets/images/about/partners.jpg"
import tea from "../assets/images/about/team.jpg"
import vicc from "../assets/images/about/vicc.JPG"

const Ourstory = () =>{

    useEffect(() => {
        AOS.init({duration: 1400});
        AOS.refresh();
    }, []);


    return(
        <>
        <Wrapper>
            <Content>
                <div className="row mt-8" >
                    <div className="col" data-aos='fade-right'> 
                        <img src={xav} alt="game" className="rounded-lg"/>
                    </div>
                    <div className="col text mb-4" data-aos='fade-left'>
                        <h3>Hello, fellow intrepid travellers,  </h3>
                        <p>Xavier Gumunyu here from Cape Archives Tours. The story of our tour company is one I often share with clients, and after six years in the tourism industry, Im proud of where this journey has taken us. Coming from a small town in Zimbabwe, I was always curious about the world and other cultures. It spurred my passion to become a tour guide in Cape Town in 2015.</p>
                    </div>
                </div>
                <div className="row" >
                    <div className="col text1" data-aos='fade-right'>
                        <p>Over the years, I formed connections with clients, and after much encouragement from one of them, I ventured into the world of tour operators and started my own company in 2018. Youre probably wondering; why call it Cape Archives Tours? As someone working in the tourism sector, theres a wealth of information on Cape Town archived in my mind, be it where to stay, what places to visit and where to eat - Im that guy.</p>
                    </div>
                    <div className="col" data-aos='fade-left'> 
                        <img src={vic} alt="connected" className="rounded-lg"/>
                    </div>
                </div>
                <div className="row" >
                    <div className="col" data-aos='fade-right'> 
                        <img src={part} className="rounded-lg"/>
                    </div>
                    <div className="col text" data-aos='fade-left'>
                        <p> What I love about my job is that I get to make beautiful memories for people across the globe who have high expectations when visiting South Africa. So, when starting my own business, I was filled with excitement and a sense of giddy fear. I remember being in Johannesburg when our first online enquiry came through. Since then, Ive never looked back. Looking back, it was quite extraordinary how things took off that fast, from booked tours to good online reviews. </p>
                    </div>
                </div>
                <div className="row" >
                    <div className="col text1" data-aos='fade-right'>
                    <h3> What could go wrong?</h3>
                        <p> A few months after Cape Archives Tours took off, the pandemic hit. The world went into lockdown, travel restrictions were in place, and the tourism industry felt it the hardest. We werent immune to the massive economic blow. Cape Archives Tours was forced to shut down for two years; we lost our vehicles and staff. Thankfully, not our sanity. It was a huge sigh of relief when the world opened up again, and this time we were more than ready. Like the first time, that rush of excitement when picking up a booking post-Covid was the same. The irony is that it was cancelled the very next day. But we persevered and in the Summer of 2021, Cape Archives Tours courted locals by offering discounted rates. This also enabled us to brush up on our tour guide expertise.</p>
                    </div>
                    <div className="col" data-aos='fade-left'> 
                        <img src={tea} alt="connected" className="rounded-lg"/>
                    </div>
                </div>
                <div className="row" >
                    <div className="col" data-aos='fade-right'> 
                        <img src={vicc} className="rounded-lg"/>
                    </div>
                    <div className="col text" data-aos='fade-left'>
                        <p>  Today, the company has grown from strength to strength. We have a new fleet of vehicles, new offices and new additions to the family, Including friends, immediate and extended family members.I couldnt have asked for a better team to make our family-run business happen. And just because were Cape Town-based, it doesnt mean the tour stops here. Plans for the future include expanding into other countries on the continent, namely Botswana and Victoria Falls. </p>
                    </div>
                </div>
                
            </Content>
        </Wrapper>
        <div className="text-center p-8 bg-[#020617] text-white ">Thank you to our clients for entrusting us to make memories with you. Xavier</div>
        </>
    )
}

export default Ourstory;